<template>
    
    <nav class="content-wrapper fixed top-0 z-50 w-full border-myIndigo border-t-4 text-mytext-primary bg-background-primaryLight shadow-xl">
        <div class="flex items-center justify-between py-3 px-5">
            <div class="font-semibold text-xl font-mono">M.Hard</div>
            <div>
                <i class="fa fa-spinner fa-pulse fa-fw"></i>
                <span class="text-xs font-bold ">Still under maintenance</span>
            </div>
            <div class="flex items-center">
              <span @click.prevent="toggleTheme" class="cursor-pointer mr-3">
                <i v-if="theme == 'theme-light'" class="fa fa-moon-o"></i>
                <i v-else class="fa fa-sun-o"></i>
              </span>
              <div id="descktop" class="hidden md:block">
                  <div class="flex flex-wrap items-center">
                      <a href="contact" class="cursor-pointer mr-3">Contact</a>
                      <i class="fa fa-2x fa-github"></i>
                  </div>
              </div>
              <div id="phone" class="block md:hidden">
                <button @click="isOpen = !isOpen" class="px-2 focus:outline-none">
                    <i v-if="isOpen" class="fa fa-remove fa-lg"></i>
                    <i v-if="!isOpen" class="fa fa-reorder"></i>
                </button>
              </div>
            </div>
        </div>
        <div :class="isOpen ? 'block':'hidden'" class="md:hidden text-left px-2 pt2 pb-4">
            <a href="#" class="block px-2 py-1 rounded-lg hover:bg-gray-400">Contact</a>
            <a href="#" class="block px-2 py-1 rounded-lg hover:bg-gray-400">my page in githup <i class="fa fa-github"></i></a>
        </div>

    </nav>

</template>

<script>
export default {
  name: 'Navbar',
  data() {
      return {
          isOpen: false,
          theme: 'theme-light',
      }
  },
  methods: {
    toggleTheme(){
      this.theme = this.theme == 'theme-light' ? 'theme-dark' : 'theme-light';
      localStorage.setItem('theme',this.theme)
      this.dataForApp();
    },
    dataForApp(){
      this.$emit('dataForApp', this.theme);
    }
  },
  created() {
    this.theme = localStorage.getItem('theme') || 'theme-light'
    this.dataForApp();
  },
}
</script>